import React from 'react';
import {Grid, Cell, Chip, ChipContact } from 'react-mdl';
import lamido from './lamido.jpg'


class LandingPage extends React.Component {
  render() {
    return (
      <div style={{width: '100%', margin: 'auto'}}>
      <Grid className="landing-grid">
      <Cell col={12}>
      <img src={lamido}
      alt="avatar"
      className='avatar-img'/>
      <div className="banner-text">
      <h1>Full Stack Web Developer</h1>
      <hr/>
      <p><Chip>
    <ChipContact style={{ background: 'url("https://seeklogo.com/images/N/nodejs-logo-FBE122E377-seeklogo.com.png") 0 0 / cover' }} className="mdl-color--teal mdl-color-text--white"></ChipContact>
    JavaScript
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">J</ChipContact>
    Java
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">RB</ChipContact>
    Ruby on Rail 
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">R</ChipContact>
    ReactJs
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">RN</ChipContact>
    React Native
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">AD</ChipContact>
    Android
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">E</ChipContact>
    ExpressJs
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">ND</ChipContact>
    NodeJs
</Chip>  <Chip>
    <ChipContact className="mdl-color--teal mdl-color-text--white">G</ChipContact>
    Git
</Chip>  <Chip>
    <ChipContact style={{ background: 'url("https://user-images.githubusercontent.com/7585388/27636865-e6505c60-5c0d-11e7-92d1-1adc1ac81a11.png") 0 0 / cover' }} className="mdl-color--teal mdl-color-text--white"></ChipContact>
    Command Line
</Chip></p>
      
      <div className="social-links">
      <a href="http://github.com" rel="noopener noreferrer" target="_blank">
      <i className="fa fa-github-square" aria-hidden="true"/>
      </a>
      <a href="http://github.com" rel="noopener noreferrer" target="_blank">
      <i className="fa fa-linkedin-square" aria-hidden="true"/>
      </a>
      <a href="http://github.com" rel="noopener noreferrer" target="_blank">
      <i className="fa fa-free-code-camp" aria-hidden="true"/>
      </a>
      <a href="http://github.com" rel="noopener noreferrer" target="_blank">
      <i className="fa fa-free-code-camp" aria-hidden="true"/>
      </a>
      </div>

      </div>
      
      </Cell>
      </Grid>
      </div>
    );
  }
}

export default LandingPage;


