import React, { Component } from 'react';
import './App.css';
import  'react-mdl'
import LandingPage from '../Layouts/LandingPage'
import Project from '../Layouts/Projects'
import MyResume from '../MyResume/MyResume'
import MyContact from '../Contact/MyContact'
import { Layout, Tab, Header, HeaderRow, HeaderTabs, Drawer, Content } from 'react-mdl'



class App extends Component {
  constructor(props) {
    super(props)
    this.state = { activeTab: 0 };
    this.setTabIndex = this.setTabIndex.bind(this)
}

handleTabToggle(tab){
  if(tab === 0 ){
    return <LandingPage/>
  }
  else if (tab === 1){
    return <Project/>
  }
  else if (tab === 2){
    return <MyResume/>
  }
  else if (tab === 3){
    return 
  }
  else if (tab === 4){
    return <MyContact/>
  }
}

setTabIndex(tabId){
  this.setState({ activeTab: tabId })
}

  render() {
    return (
      <div  className="header-color">
      <Layout fixedHeader fixedTabs >
          <Header className="header1-color">
              <HeaderRow title="Title"  />
              <HeaderTabs   ripple onChange={this.setTabIndex}>
                  <Tab >Home</Tab>
                  <Tab>Project</Tab>
                  <Tab>Resume</Tab>
                  <Tab>About</Tab>
                  <Tab>Contact</Tab>
              </HeaderTabs>
          </Header>
          <Drawer title="Title" />
          <Content>
              <div className="page-content">{this.handleTabToggle(this.state.activeTab)}</div>
          </Content>
      </Layout>
  </div>
    );
  }
}

export default App;



